.entry {
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    max-width: 880px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 60px auto 80px;
}

@media screen and (max-width: 450px) {
    .entry {
        margin: 40px auto 60px;
    }
}